/* 初始設定 */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap');
* {
  margin: 0;
  padding: 0;
  border: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-family: 'Inter', 'Noto Sans TC', 'Montserrat', 'Josefin Sans', sans-serif;
}
body {
  display: flex;
  font-size: 16px;
  color: #333;
  background: #0f0f0f;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
ul,
ol {
  list-style: none;
}
:focus {
  outline: none;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
* {
  user-select: none;
}
#root {
  height: 100%;
  width: 100%;
}
.App {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  background: #282c34;
}

/* 頂部欄 */
.App-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  height: 54px;
  width: 100%;
  font-size: calc(10px + 2vmin);
  color: white;
  background: #30353f00;
}

/* 頂部欄標題 */
.App-header .headerL {
  display: flex;
  align-items: center;
  padding: 12px 18px;
}
.App-header .headerL > div {
  display: flex;
  align-items: center;
}
.App-header .headerL > div svg {
  margin-right: 12px;
}

/* 頂部欄選單 */
.App-header .headerR {
  display: flex;
  align-items: center;
  padding: 12px 18px;
}
.App-header .headerR > div {
  display: flex;
  align-items: center;
}

/* 主頁 */
main {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 54px 0 0 0;
}
main > section {
  width: calc(100% - 24px);
  padding: 12px;
  color: #f1f1f1a1;
}

/* 記帳功能 */
section > .soldBlock {
  display: flex;
  flex-direction: column;
  justify-content: end;
  margin-bottom: 6px;
  padding: 14px 18px;
  height: 30vh;
  width: 100%;
  background: #5c6982;
  border-radius: 23px 23px 9px 9px;
  -webkit-border-radius: 23px 23px 9px 9px;
  -moz-border-radius: 23px 23px 9px 9px;
  -ms-border-radius: 23px 23px 9px 9px;
  -o-border-radius: 23px 23px 9px 9px;
}
.soldBlock h1 {
  text-align: start;
  width: 100%;
  color: #ffffffa1;
}
.soldBlock h3 {
  text-align: start;
  width: 100%;
  color: #ffffff7f;
}

section > .buyBlock {
  display: flex;
  flex-direction: column;
  justify-content: start;
  padding: 12px 18px;
  height: 10vh;
  width: 100%;
  background: #465165;
  border-radius: 9px 9px 23px 23px;
  -webkit-border-radius: 9px 9px 23px 23px;
  -moz-border-radius: 9px 9px 23px 23px;
  -ms-border-radius: 9px 9px 23px 23px;
  -o-border-radius: 9px 9px 23px 23px;
}
.buyBlock h1 {
  text-align: start;
  width: 100%;
  color: #ffffffa1;
}
.buyBlock h3 {
  text-align: start;
  width: 100%;
  color: #ffffff7f;
}
